import React from 'react';
import { useRouteMatch } from 'react-router-dom';
import { NavLink, useHistory } from 'react-router-dom';

const Header = (props) => {
    const history = useHistory();
    const logout = () => {
        // localStorage.removeItem("accessAcc");
        // localStorage.getItem("admin") ? localStorage.removeItem("admin") : localStorage.removeItem("mem");
        // history.replace('/login')
    };

    //=======> ACTIVE LINK <========//

    //====Home ====//
    const Home = () => {
        const match = useRouteMatch({
            path: '/home',
        });
        return (
            <li>
                <NavLink
                    className={match ? 'nav__link active_link' : 'nav__link'}
                    to='/home'
                >
                    <i className='fa fa-home' aria-hidden='true'></i> Home
                </NavLink>
            </li>
        );
    };

    //====Departments ====//
    const Departments = () => {
        const match = useRouteMatch({
            path: '/departments',
        });
        return (
            <li>
                <NavLink
                    className={match ? 'nav__link active_link' : 'nav__link'}
                    to='/departments'
                >
                    <i className="fa fa-list-ul" aria-hidden="true"></i> Danh Mục
                </NavLink>
            </li>
        );
    };

    //==== Menu ====//
    const Menu = () => {
        const match = useRouteMatch({
            path: '/menu',
        });
        return (
            <li>
                <NavLink
                    className={match ? 'nav__link active_link' : 'nav__link'}
                    to='/menu'
                >
                    <i className="fa fa-cutlery" aria-hidden="true"></i> Nổi bật
                </NavLink>
            </li>
        );
    };
    //=====About=====//
    const About = () => {
        const match = useRouteMatch({
            path: '/about',
        });
        return (
            <li>
                <NavLink
                    className={match ? 'nav__link active_link' : 'nav__link'}
                    to='/about'
                >
                    <i className="fa fa-info-circle" aria-hidden="true"></i> Về tôi
                </NavLink>
            </li>
        );
    };
    //==== Contact ====//
    const Contact = () => {
        const match = useRouteMatch({
            path: '/contact',
        });
        return (
            <li>
                <NavLink
                    className={match ? 'nav__link active_link' : 'nav__link'}
                    to='/contact'
                >
                    <i className="fa fa-phone-square" aria-hidden="true"></i> Liên Hệ
                </NavLink>
            </li>
        );
    };

    return (
        <React.Fragment>
            <nav className='sticky-top'>
                <div className='menu-icon'>
                    <i className='fa fa-bars fa-2x'></i>
                </div>
                <div className='logo'>
                    <img
                        className='btMainLogo'
                        data-hw='4.5277777777778'
                        src='/assets/images/CHILL.png'
                        alt='AISolutions'
                        width='100px'
                    />
                </div>
                <div className='menu'>
                    <ul>
                        <Home />
                        <Departments />
                        <Menu />
                        <About />
                        <Contact />
                        {/* <li>
                            <NavLink className='nav-link nav__link' to='/login'>
                                <span
                                    onClick={logout}
                                    className='fa fa-sign-out fa-lg'
                                ></span>{' '}
                            </NavLink>
                        </li> */}
                        {/* <li>
                            <NavLink className='nav-link nav__link' to='/information'>
                                <span className='fa fa-user fa-lg'></span>{' '}
                            </NavLink>
                        </li> */}
                    </ul>
                </div>
            </nav>
        </React.Fragment>
    );
};
export default Header;



